<template>
  <div id="wa">
    <h3>请输入分析内容</h3>
    <hr>
    <textarea class="input" v-model="txtmsg" ref="input" id="input"></textarea>
    <hr>
    <div type="file" class="buttom" @click="to_file"><span>从数据中心导入文件</span><img src="" alt="显示图片"></div>
    <div class="buttom" @click="cls_txt">清除内容</div>
    <div class="buttom" id="to_wa2" @click="to_wa2">分析文本</div>
  </div>
  <input type="file" name="" ref="aaa" @change="file_change($event)">
</template>

<script>
import axios from 'axios';
axios.defaults.headers.post['Content-Type'] = 'application/x-www-fromurlencodeed';
const vc =  {
    data(){
        return({
            txtmsg:'',
        })
    },
    methods:{
        cls_txt(){
            this.txtmsg='';
        },
        to_wa2(){
            this.$router.push({
                name:'词性分析2',
                query:{
                    str_1:this.txtmsg,
                }
            // alert(this.txtmsg);
            })
        },
        to_file(){
            this.$refs.aaa.click();
        },
        file_change(e){
            let file = e.target.files[0];
            let filedata = new FormData;
            filedata.append('file', file);
            // console.log(filedata.values);
            console.log('开始ajax');
            axios({
                url: ' http://127.0.0.1:8080/read_file',
                data: filedata,
                method: 'post',
                headers: { 'Content-Type': 'multipart/form-data'},
            }).then(
                response => {
                    console.log(response.data);
                    console.log(typeof response.data);
                    this.txtmsg = response.data.text;
                },
                error => {
                    console.log(error.message);
                }
            );
            console.log('结束ajax');
        }
    },
    mounted(){
        // const reader = new FileReader();
    }
}
export default vc
</script>

<style>
    #wa{
        margin-top: 350px;
        margin-left: 600px;
        background-color:bisque;
        height: 800px;
        width: 1500px;
        border-radius: 5%;
        outline-style: none ;
        border: 0px;
        padding:10px 25px;
    }
    .input{
        border: 0px;
        width: 100%;
        height:650px;
        outline-style: none ;
        font-size: larger;
        background-color: bisque;
        resize: none;
    }
    .buttom{
        display: inline-block;
        background-color:aqua;
        border-radius: 20%;
        margin-right: 15px;
        height: 40px;
        padding: 0px 2px;
        line-height: 40px;
    }
    #to_wa2{
        background-color: red;
        float: right;
    }
    .input::-webkit-scrollbar {
	 width: 6px;
	 height: 6px;
    }
    .input::-webkit-scrollbar-thumb {
        border-radius: 3px;
        -moz-border-radius: 3px;
        -webkit-border-radius: 3px;
        background-color: #c3c3c3;
    }
    .input::-webkit-scrollbar-track {
        background-color: transparent;
    }
</style>